/* ProjectDiary.css */

/* Style for the main container */
.project-diary {
    padding: 20px;
    background-color: #fff; /* White background color */
}

    /* Style for the heading */
    .project-diary h1 {
        font-size: 32px;
        margin: 0; /* Remove margin to prevent unwanted spacing */
    }

    /* Style for the button to show/hide entries */
    .project-diary button {
        margin-top: 10px;
        font-size: 18px;
        padding: 5px 10px;
        background-color: #333; /* Blue button color */
        color: #fff; /* Text color */
        border: none;
        border-radius: 3px;
        cursor: pointer;
        transition: background-color 0.3s ease-in-out;
    }
    /* Diario.css */
    /* Add these styles to adjust image size */
    .project-diary .diario-image {
        max-width: 100%; /* Adjust the maximum width of the images */
        height: auto; /* Maintain aspect ratio */
        /* Additional styling as needed */
    }


/* Style for the table */
.diary-table {
    width: 100%;
    border-collapse: collapse; /* Remove spacing between table cells */
    margin-top: 10px;
}

    /* Style for table headers */
    .diary-table th {
        background-color: #333; /* Header background color */
        color: #fff;
        font-weight: bold;
        padding: 8px;
    }

    /* Style for table rows */
    .diary-table td {
        border: 1px solid #ccc;
        padding: 8px;
        text-align: left;
    }

    /* Style for alternating row background color */
    .diary-table tbody tr:nth-child(even) {
        background-color: #f2f2f2;
    }

/* Style for button hover effect */
.project-diary button:hover {
    background-color: #555; /* Darker blue on hover */
}
@media (max-width: 768px) {
    /* Reduce padding and font size for better mobile layout */
    .project-diary {
        padding: 0px;
        margin-left: -5px;
    }

        .project-diary h1 {
            font-size: 24px;
        }

        .project-diary button {
            font-size: 16px;
        }
    .titulodiario{
        padding: 10px;
    }
    .botaodiario{
        padding: 0 0 10px 10px;
    }
}